<template>
  <div>
    <Preloader :preloader="preloader" />
    <Navbar />
    <slot>
      <!-- content comes here -->
    </slot>
    <Footer />
  </div>
</template>

<script>
import Navbar from "./Navbar";
import Preloader from "./Preloader";
import Footer from "./Footer";

export default {
  name: "Wrapper",
  components: { Navbar, Preloader, Footer },
  data() {
    return {
      preloader: true
    };
  },

  created() {
    //  close preloader
    setTimeout(() => {
      this.preloader = false;
    }, 450);
  }
};
</script>